			display: none !important; } } }
&.has-tooltip-hidden-desktop-only {
	@include desktop-only {
		&::after,
		&::before {
			opacity: 0 !important;
			display: none !important; } } }
&.has-tooltip-hidden-until-widescreen {
	@include until-widescreen {
		&::after,
		&::before {
			opacity: 0 !important;
			display: none !important; } } }
&.has-tooltip-hidden-widescreen {
	@include widescreen {
		&::after,
