	&.has-tooltip-text-#{$dir}-until-fullhd {
		@include until-fullhd {
			&::before {
				text-align: #{$text}; } } }
	&.has-tooltip-text-#{$dir}-fullhd {
		@include fullhd {
			&::before {
				text-align: #{$text}; } } } }
